@import url(https://fonts.googleapis.com/css2?family=Alegreya&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
/* Styles for the Techstack component */

.tech__title {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 3rem;
}

.tech__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.tech__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  padding: 1rem;
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  -webkit-animation: slideFromLeft 1s ease-out;
          animation: slideFromLeft 1s ease-out;
}

.tech__logo {
  width: 100px;
  margin-bottom: 1rem;
}

.tech__name {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  color: #2368a2;
}

/* Animation keyframes */

@-webkit-keyframes slideFromLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFromLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Mobile styles */
@media (max-width: 600px) {
  .tech__title {
    font-size: 1.5em;
  }

  .tech__item {
    margin: 0.5em;
  }

  .tech__logo {
    width: 60px;
    margin-bottom: 0.2em;
  }

  .tech__name {
    font-size: 1em;
  }

  .tech__list {
    flex-wrap: wrap;
  }
}

.homepage__header {
  display: flex;
  min-height: 50vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homepage__name {
  font-size: 5em;
}

.homepage__role {
  text-align: center;
  font-size: 25px;
  color: #a9a9ac;
}

.homepage__content {
  text-align: center;
}

@media (max-width: 500px) {
  .homepage__name {
    font-size: 4em;
  }

  .homepage__header {
    padding-top: 10%;
  }
}

.portfolio {
  margin: 8em 0;
}

.portfolio__title {
  margin-left: 8px;
}

.portfolio__experience {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 5em 5em 5em;
}

.portfolio__experience--category {
  text-align: center;
  color: #141414;
}

.portfolio__experience__card,
.portfolio__education {
  background-color: #ffffff;
  border: solid 2px #bdb7b7;
  padding: 1em;
  margin: 1em;
  text-align: center;
  min-width: 350px;
}

.portfolio__experience--title,
.portfolio__wrapper--title {
  font-weight: 650;
  text-align: center;
  margin-top: 2em;
}

.cv__wrapper {
  justify-content: space-between;
}

.portfolio__wrapper {
  display: flex;
  justify-content: center;
}

.portfolio__page {
  margin-bottom: 15%;
}

.portfolio__education {
  margin-top: 2em;
  background-color: #ffffff;
  border: solid 2px #bdb7b7;
  flex-basis: 50%;
}

.portfolio__education p {
  font-size: 17px;
}

.portfolio__experience--category__wrapper {
  display: flex;
  justify-content: space-between;
}

.iconify {
  width: 50px;
}

.portfolio__education img {
  display: flex;
  max-width: 100%;
}

.portfolio__button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #007bff;
  border: none;
  transition: background-color 0.2s ease-in-out;
  margin-bottom: 8px;
}

.portfolio__button:hover {
  background-color: #0062cc;
  cursor: pointer;
}

.portfolio__experience__card--image {
  max-width: 100%;
    height: auto;
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
}

.portfolio__article--paragraph {
  font-size: 16px;
}

@media (max-width: 500px) {
  .portfolio__education p {
    font-size: 12px;
  }

  .portfolio__experience {
    margin: 0;
  }

  .portfolio__experience--category {
    font-size: 20px;
  }

  .portfolio__experience--title,
  .portfolio__wrapper--title {
    font-size: 20px;
    margin: 15px 0 0 8px;
  }

  .portfolio__experience,
  .portfolio__wrapper {
    flex-direction: column;
  }

  .portfolio__education img {
    width: 300px;
  }
}


.primaryButton {
  padding: 8px;
  margin: 5px;

  background-color: transparent;
  color: black;

  border: 2px solid #bdb7b7;
}

.primaryButton:hover {
  cursor: pointer;
  border-color: black;
}


.articleLink {
  color: black;
}

.articleLink:hover {
  text-decoration: none;
  color: black;
}

.cv__wrapper {
  display: flex;
  color: white;
  margin: 0 0 2em;
}

.cv__description--edu {
  color: black;
}

.cv__study {
  text-align: left;
}

.cv__view-subjects {
  text-decoration: underline;
}

.cv__wrapper p {
  margin: 1em;
  color: #343a40;
  font-size: 14px;
}

.cv__date {
  font-weight: 650;
  color: #1a4971;
}

.cv__wrapper img {
  max-height: 70px;
  max-width: 70px;
}

.project__wrapper,
.project__card,
.button__wrapper {
  display: flex;
  justify-content: space-evenly;
}

.project__card {
  flex-basis: 70%;
  margin: 1em;
}

.project__text {
  font-size: 13pt;
  text-align: center;
}

.project__topic {
  color: black;
}

.project__title {
  text-align: center;
  font-size: 1.4em;
  color: #141414;
  margin: 1em;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 800;
}

.project__wrapper {
  margin-top: 2em;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 10em;
}

.project__image {
  object-fit: cover;
  height: 250px;
}

.link__button {
  margin-bottom: 1em;
}

.btn:hover {
  color: green;
}

.primaryButton {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #007bff;
  border: none;
  transition: background-color 0.2s ease-in-out;
}

.primaryButton:hover {
  background-color: #0062cc;
  cursor: pointer;
}

@media (max-width: 768px) {
  .portfolio__experience--title {
    margin-top: 4em;
  }

  .project__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .project__image {
    height: 300px;
  }

  .project__title {
    font-size: 20px;
  }
}


#root {
  overflow-x: hidden;
}

.app__wrapper {
  max-width: 1200px;
  margin: 0 auto;
}


.navbar__routes,
.footer__links {
  padding: 1em;
}

.navbar__routes {
  background-color: #fbfbfd;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 2px solid rgba(38, 38, 38, 0.1);
}

.navbar__routes ul,
.footer__links ul {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
  list-style-type: none;
  text-transform: uppercase;
  margin: 0;
}

.navbar__routes img {
  width: 25px;
}

.mode__toggle:hover {
  cursor: pointer;
}

.navbar__routes a,
.navbar__routes a:hover,
.footer__links a,
.footer__links a:hover,
.footer__links p {
  text-decoration: none;
  font-weight: 650;
  padding: 5em;
}
.navbar__routes a,
.footer__links a {
  color: #333333;
}

.navbar__routes a:hover,
.footer__links a:hover {
  color: #3183c8;
}

.footer__links {
  background-color: #f5f5f7;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

body {
  background-color: #fbfbfd;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background: rgb(228, 228, 228);
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(180deg, #d0368a 0%, #708ad4 99%);
  box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
  border-radius: 100px;
}

@media (max-width: 500px) {
  .navbar__routes,
  .footer__links {
    font-size: 0.8em;
  }

  .homepage__development {
    text-align: center;
  }

  .navbar__routes ul,
  .footer__links ul {
    justify-content: space-between;
  }

  .navbar__routes a,
  .navbar__routes a:hover,
  .footer__links a,
  .footer__links a:hover {
    font-weight: 800;
    padding: 1em;
  }

  ul {
    padding: 0;
  }
}

@media (max-width: 330px) {
  .navbar__routes,
  .footer__links {
    font-size: 0.6em;
  }
}

